.all-cars {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
    padding-bottom: 2rem;
}

.car-card{
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.1);
    background-color: var(--nav-bg)
}
.car-card img{
    height: 270px;
    object-fit: cover;
}


.car-card__text{
    padding-inline: 10px;
    padding-block: 10px;
}
.car-card__text h3{
    margin-bottom: 10px;
    text-transform: uppercase;
    font-size: 16px;
}
.car-card__text h4{
    font-weight: 500;
    margin-bottom: 5px;

}
.car-card__text > *{
    /* margin-bottom: 5px; */
}
.car-card__text p{
    font-size: 14px;
}


.car-card__btn{
    margin-top: 10px;
    padding: 10px 1rem;
    border-radius: 0px;
    display: inline-block;
    background-color: var(--bg-dark);
}
.car-card__btn a{
    color: var(--bg-light);
    cursor: pointer;
}





@media (max-width: 1200px) {}

@media (max-width: 992px) {
    
}

@media (max-width: 768px) {
    .all-cars {
        grid-template-columns: 1fr 1fr ;
    }
}

@media (max-width: 576px) {
    .all-cars {
        grid-template-columns: 1fr ;
    }
}